import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			imgFile: '',
			nickName: '',
			sex: '',
			sexValue: '',
			sexList: [{label: '男',value: 1},{label: '女',value: 2}],
			heightList: [],
			heightValue: '',
			birthValue: '',
			defaultValBirth: ['1990','06','15'],
			workList: [{label: '',value: ''},{label: '',value: ''}],
			workId: '',
			workValue: '',
			regionId: '',
			regionValue: '',
			selectedIds: '',
			selectedList: [{label: '羽毛球',value: 1},{label: '跑步',value: 2},{label: '自驾游',value: 3},{label: '自驾游自驾游',value: 3}]
		}
	},
	onLoad(options) {
		// 获取信息
		this.$http.get({
			url: '/v1/rest/mydata/findUserInfo',
			data: {
				userId: uni.getStorageSync('userInfo').userId
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.imgFile = resp.data.headPortrait
				this.nickName = resp.data.nickName
				this.sex = resp.data.sex
				if(resp.data.sex == 1) {
					this.sexValue = '男'
				}else{
					this.sexValue = '女'
				}
				this.heightValue = resp.data.height
				this.birthValue = resp.data.birthday
				this.workId = resp.data.occupationId
				this.workValue = resp.data.occupation
				this.regionId = resp.data.region
				this.regionValue = resp.data.regionName
				this.selectedIds = resp.data.labelId
				this.selectedList = resp.data.labelVoList
			}
		})
		// 身高选择列表
		for(let i=120; i<=200; i++) {
			let item = {
				label: i + ' cm',
				value: i
			}
			this.heightList.push(item)
		}
		// 职业选择列表
		this.$http.get({
			url: '/v1/rest/public/findDictList',
			data:{
				skey: 'occupation',
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.workList = resp.data
			}
		})
	},
	onShow() {
		
	},
	methods: {
		choiceImg() {
			const that = this
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function (res) {
					console.log(res)
					that.imgFile = res.tempFilePaths[0]
					const filepath = res.tempFilePaths[0]
					uni.showLoading({
					  title: '上传中...'
					})
					uni.uploadFile({
						url: that.$http.baseUrl + '/v1/rest/file/uploadOSS', //仅为示例，非真实的接口地址
						filePath: filepath,
						name: 'file',
						success: (uploadFileRes) => {
							uni.hideLoading();
							const resp = JSON.parse(uploadFileRes.data)
							console.log(resp);
							if(resp.status == 200) {
								that.imgFile = resp.data[0]
								uni.showToast({
									title: '上传成功！',
									duration: 1500,
									icon: 'none'
								});
							}
						},
						fail: (err) => {
							console.log(err)
						}
					});
				}
			});
		},
		InputNickName(v) {
			this.nickName = v.detail
		},
		selectSex() {
			this.$refs.picker1.show()
		},
		confirmSex(v) {
			this.sexValue = v.result
		},
		// 选择身高
		selectHeight(v) {
			this.$refs.picker2.show()
		},
		confirmHeight(v) {
			this.heightValue = v.result
		},
		// 选择生日
		selectBirth(v) {
			this.$refs.picker3.show()
		},
		confirmBirth(v) {
			this.birthValue = v.result
		},
		// 选择职业
		selectWork(v) {
			this.$refs.picker4.show()
		},
		confirmWork(v) {
			this.workValue = v.result
		},
		// 选择地区
		selectRegion(v) {
			this.$refs.picker5.show()
		},
		confirmRegion(v) {
			this.regionValue = v.result
		},
		// 选择标签
		selectLabel() {
			uni.navigateTo({
				url: '/pages/userInfo/tagsPage/tagsPage'
			})
		},
		//保存
		submit() {
			
		}
	}
}
